.library {
    position: fixed;
    top: 0;
    left: 0;
    width: 20rem;
    height: 100%;
    background-color: white;
    box-shadow: 2px 2px 30px rgb(142, 142, 142);
    overflow: scroll;
    h2 {
        padding: 2rem;
    }
    transform: translateX(-100%);
    transition: all 0.5s ease;
    opacity: 0;
}

.library-song {
    display: flex;
    align-items: center;
    
    padding: 1rem 2rem 1rem 2rem;

    img {
        width: 40%;
    }
    cursor: pointer;
    &:hover {
        background: rgb(227, 249, 255);
    }
    transition: 0.3s ease;
}

.song-description {
    padding-left: 1rem;
    h3 {
        font-size: 1.2rem;
    }
    h4 {
        font-size: 0.9rem;
    }
}

* {
    scrollbar-width: thin;
    scrollbar-color: rgba(155, 155, 155, 0.5) transparent;
}

*::-webkit-scrollbar {
    width: 5px;
}

*::-webkit-scrollbar-track {
    background: transparent;
}

*::-webkit-scrollbar-thumb {
    background-color: rgba(155, 155, 155, 0.5);
    border-radius: 20px;
    border: transparent;
}

.selected {
    background-color: rgb(227, 249, 255);
}

.active-library {
    transform: translateX(0);
    opacity: 1;
}

@media screen and (max-width: 768px) {
    .library {
        width: 100%;
    }
}